@import '../../../index.scss';


//Container
.aboutTextContainer {
    width: 45%;
    height: 90%;
    display: flex;
    flex-direction: column;
    transition: 250ms;
}

//Container end

//Content

.title {
    font-family: MontSerratBold;
    font-size: calc(3vw + 1em);
    color: $midnightBlue;
    margin-bottom: 0.7em;
}

.text {
    font-family: MontSerrat;
    font-size: calc(0.8vw + 0.4em);
    text-align: justify;
    color: $midnightBlue;
}

//Content

//Window adjust media

@media (max-width: 700px) {

    //Container 
    .aboutTextContainer {
        width: 80%;
    }

    //Content

    .title {
        font-size: calc(3vw + 1.5em);
    }

    .text {
        font-size: calc(0.8vw + 0.6em);
    }
}