@import '../../../index.scss';

.ContactInfoContainer {
    width: 45%;
    height: 80%; 
    overflow: clip;
    position: relative;
}

.ContentContainer {
    width: 100%;
    height: 100%;
    display: flex;  
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: -5vw;
}

//Content

.title {
    width: 90%;
}

.title h1 {
    width: 100%;
    margin-bottom: 0;
    text-align: left;
    font-family: MontserratBold;
    font-size: calc(1.5vw + 1em);
    color: #fff;
    text-transform: uppercase;
}

.title p {
    width: 100%;
    margin-top: 0;
    text-align: left;
    font-family: Montserrat;
    font-size: calc(1vw + 0.8em);
    color: #fff;
}

.contactPlataformsContainer {
    width: 100%;
}

.contactBackground {
    width: 98%;
    margin: 2vw 0 3vw 0;
    padding: 0.4vw;
    display: flex;
    align-items: center;
    background: linear-gradient(to right, #404BE3, #00ABFF);
    border-radius: 7%/50%;
    transition: 500ms;
    text-decoration: none;
}

.contactBackground:hover {
    cursor: pointer;
    background: linear-gradient(to right, #3a43c9, #028dd3);
    transition:  500ms;
}

.contactBackground label:hover {
    cursor: pointer;
}

.contactBackground img {
    width: calc(3.5vw + 1em);
    margin-left: 2vw;
}

.contactBackground label {
    margin-left: 1vw;
    font-family: Poppins;
    font-weight: 600;
    font-size: calc(1vw + 0.35em);
    color: #fff;
}

//Content end

//Media mobile

@media (max-width: 499px) {

    //Container

    .ContactInfoContainer {
        width: 90%;
        height: 40%;
    }

    //Container end  

    //Content

    .title {
        width: 100%;
    }

    .title h1 {
        text-align: center;
        font-size: calc(2vw + 1em);
    }

    .title p {
        text-align: center;
        font-size: calc(1vw + 1em);
    }

    .contactPlataformsContainer {
        width: 50%;
        display: flex;
        justify-content: space-evenly;
        margin: 0 auto;
    }

    .contactBackground {
        width: fit-content;
        background: transparent;
    }

    .contactBackground:hover {
        background: transparent;
    }

    .contactBackground img {
        width: 4em;
        margin-left: 2vw;
    }

    .contactBackground label {
        display: none;
    }

    //Content end
}