@import '../../../index.scss';

//Container

.mobileNavBarContainer {
    width: 100%;
    height: 5em;
    padding-left: 10%;
    display: none;
    align-items: center;
    position: absolute;
    top: 1em;
    background: #151531cb;
    z-index: 999999;
}

.mobileNavBarContainerAfterScroll {
    width: 100%;
    height: 5em;
    padding-left: 10%;
    display: none;
    align-items: center;
    position: fixed;
    top: 0;
    z-index: 99999;
    opacity: 0;
    transition: opacity 200ms;
}



//Container end

//Elements

.logoImage {
    width: 5em;
    animation: linear rotating 20s infinite;
}

.menuButton {
    width: 35px;
    height: 35px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    cursor: pointer;
    position: absolute;
    right: 10%;
    -webkit-tap-highlight-color: transparent;
}

.menuButton input[type="checkbox"] {
    width: 35px;
    height: 35px;
    display: none;
}

.menuButton span {
    width: 100%;
    height: 4px;
    background-color: #fff;
    border-radius: 10%/50%;
    transition: .4s cubic-bezier(.89,.19,.17,.86);
}

.menuButton input[type="checkbox"]:checked ~ span:nth-of-type(1) {
    transform-origin: left;
    transform: rotate(45deg) translate3d(2px, -5px, 0);
}

.menuButton input[type="checkbox"]:checked ~ span:nth-of-type(2) {
    transform-origin: center;
    transform: rotate(-45deg) translate3d(0px, 1px, 0);
}

.menuButton input[type="checkbox"]:checked ~ span:nth-of-type(3) {
    transform-origin: right;
    transform: rotate(45deg) translate3d(-1px, 6px, 0);
    opacity: 0;
}

.menuButton input[type="checkbox"]:checked ~ div:nth-of-type(1) {
    transition: transform 250ms;
    transform: translateX(0);
}

.afterScrollButton {
    background: #fafafa;
    box-shadow: inset 0 0 5px 5px #0000000e;
    filter: drop-shadow(2px 2px 5px #0000002d);
    border-radius: 10%;
    padding: 7px;
}

.afterScrollButton span {
    background-color: $midnightBlue;
}

.menuContainer {
    width: fit-content;
    height: fit-content;
    bottom: -11em;
    right: 0;
    position: absolute;
    display: flex;
    flex-direction: column;
    background-color: #fafafa;
    border-radius: 5%;
    transition: transform 250ms;
    transform: translateX(150%);
    box-shadow: inset 0 0 8px #00000034, 4px 4px 5px #00000033;
}

.menuContainer p {
    width: 10em;
    margin: 0;
    padding: 0.5em 1em 0.5em 0.5em;
    text-align: right;
    font-family: Poppins;
    font-weight: 600;
    color: $midnightBlue;
}


//Elements end

//Keyframe

@keyframes rotating {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(359deg);
    }
}

//Mobile 

@media (max-width: 499px) {

    //Container

    .mobileNavBarContainer {
        display: flex;
    }
    
    .mobileNavBarContainerAfterScroll {
        display: flex;
    }

    //Container end  
}