@import '../../../index.scss';

//Container

.introductionTextContainer {
    width: 50%;
    height: 100%;
    margin-top: 3em;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.iconsContainer {
    margin: 1vw 0 2vw 0;
}

//Container end

//Content

.text {
    margin: 1vw 0;
    font-size: calc(1vw + 0.5em);
    font-family: Geometria;
    color: #fff;
}

.highlightedText {
    margin: 0;
    font-size: calc(4vw + 1em);
    font-family: Poppins;
    line-height: 1;
    color: $eletricPurple;
    user-select: none;
}

//Content

//Elements

.icon {
    width: calc(3vw + 2em);
    margin-right: 1.5vw;
    user-select: none;
    transition: filter 500ms;
}

.icon:hover {
    filter: drop-shadow(0px 0px 5px $eletricPurple);
    cursor: pointer;
    transition: filter 500ms;
}

.aboutMeButton {
    width: calc(7vw + 6em);
    height: calc(1vw + 2em);
    position: relative;
    background-color: $cornFlowerBlue;
    padding-right: 30px;
    border: none;
    outline: none;
    border-radius: 10%/50%;
    color: #fff;
    font-family: Poppins;
    font-size: calc(0.8vw + 0.5em);
    font-weight: 600;
    user-select: none;
    transition: filter 500ms;
}

.aboutMeButton:hover {
    cursor: pointer;
    filter: drop-shadow(0px 0px 10px $cornFlowerBlue);
    transition: filter 500ms;
}


.aboutMeButton::after {
    content: '';
    width:calc(0.8vw + 0.4em);
    height:calc(0.8vw + 0.4em);
    position: absolute;
    top: 35%;
    right: calc(2vw + 0.3em);
    background-image: url(../../../Images/Introduction/VisualElements/downArrow.svg);
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;
    animation: pointingDown 1s infinite;
    animation-timing-function: ease-in;
    animation-play-state: paused;
}

.aboutMeButton:hover:after {
    animation-play-state: running;
}

//Elements end

//Keyframe

@keyframes pointingDown {
    0% {
        transform: translateY(0);
    }

    25% {
        transform: translateY(3px);
    }

    50% {
        transform: translateY(0)
    }

    75% {
        transform: translateY(-3px);
    }

    100% {
        transform: translateY(0)
    }
}

//Media mobile

@media (max-width: 499px) {

    //Container

    
    .introductionTextContainer {
        width: 100%;
        margin-top: 3em;
    }

    .iconsContainer {
        display: block;
        margin: 3vw auto;
    }

    //Container end  

    //Content

    .text {
        margin: 1vw 0;
        font-size: calc(1vw + .8em);
        text-align: center;
    }

    .highlightedText {
        text-align: center;
        font-size: calc(4vw + 1.8em);
    }

    //Content

    //Elements

    .aboutMeButton {
        display: block;
        margin: 0 auto;
        width: calc(7vw + 8em);
        height: calc(1vw + 3em);
        border-radius: 12%/50%;
        font-size: calc(0.8vw + 0.8em);
    }


    .aboutMeButton::after {
        width:calc(0.8vw + 0.8em);
        height:calc(0.8vw + 0.8em);
        top: 35%;
        right: calc(2vw + 0.6em);
        animation-play-state: running;
    }
}

