@import '../../../index.scss';

//Container

.IntroductionPhotoContainer {
    width: 50%;
    margin-top: 6em;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

//Container end

//Elements

.IntroductionPhoto {
    width: 30vw;
    animation: linear moving 5s infinite ;
}

//Elements end

//Keyframe

@keyframes moving {

    25% {
        transform: translate3d(10px, -10px, 0);
    }

    75% {
        transform: translate3d(-10px, 10px, 0);
    }

}

//Keyframe

//Media mobile

@media (max-width: 499px) {

    //Container

    .IntroductionPhotoContainer {
        width: 100%;
        margin-top: 2em;
        justify-content: center;
    }

    //Container end  

    //Elements

    .IntroductionPhoto {
        width: 60vw;
        animation: linear moving 10s infinite ;
    }

    //Elements end
}