@import '../../../index.scss';

.projectsTitleContainer {
    width: 40%;
    height: fit-content;
}

.title {
    margin: 0;
    color: $midnightBlue;
    font-family: Montserrat;
    padding-left: 5px;
    font-size: calc(1vw + 0.5em);
}

.responsiveTitle::after {
    content:"Ao lado, vocês podem conferir" ;
    margin: 0;
    color: $midnightBlue;
    font-family: Montserrat;
    padding-left: 5px;
    font-size: calc(1vw + 0.5em);

}

.titleHighlighted {
    font-size: calc(3.5vw + 0.5em);
    font-family: MontserratBold;
    padding-left: 0px;
    line-height: 1;
}

@media(max-width:800px) {
    .projectsTitleContainer {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .title {
        font-size: calc(2vw + 0.7em);
        text-align: center;
    }

    .titleHighlighted {
        font-size: calc(5.5vw + 0.5em);
        text-align: center;
    }
}

@media (max-width: 500px) {
    .responsiveTitle::after {
        content:"Abaixo, vocês podem conferir" ;
    }
}