@import '../../../index.scss';

.ContactImageContainer {
    width: 45%;
    height: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
}

//Elements

.ContactImageContainer img {
    width: calc(20vw + 10em);
    animation: linear moving 5s infinite;
}

//ELements end

//keyframe

@keyframes moving {
    25% {
        transform: translateY(-0.5vw);
    }

    75% {
        transform: translateY(0.5vw);
    }
}

//Media mobile

@media (max-width: 499px) {

    .ContactImageContainer {
        height: 25%;
        margin-top: 10em;
    }
    
    //Elements
    
    .ContactImageContainer img {
        width: calc(20vw + 12em);
    }
    
    //ELements end
}