@import '../index.scss';

.contactContainer {
    width: 100%;
    height: min(48vw, $defaultScreenSize);
    min-height: $minSize;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient($purpleIndigo 20%, $eletricPurple);
    position: relative;
    z-index: 9999;
    overflow: clip;
}

//Elements 

.star {
    width: calc(5vw + 4em);
    position: absolute;
    transition: all 500ms;
    animation-name: shiny;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}

.leftStar {
    animation-duration: 4s;
    top: calc(1vw + 1em);
    left: 8vw;
}

.centerStar {
    animation-duration: 8s;
    top: calc(4vw + 4em);
    right: 0;
    left: 0;
    margin-left: auto;
    margin-right: auto;
}

.rightStar {
    animation-duration: 6s;
    top: calc(0.5vw + 0.5em);
    right: 2vw;
}

//Elements end

//Keyframe

@keyframes shiny {
    0% {
        filter: drop-shadow(0 0 0 #fff);
    }

    25% {
        filter: drop-shadow(0 0 3px #fff);
    }
}

//Media mobile

@media (max-width: 499px) {

    //Container

    .contactContainer {
        flex-direction: column-reverse;
    }

    //Container end  
}