@import '../../../index.scss';

//Container
.projectsDisplayContainer {
    width: 50%;
    height: fit-content;
    overflow: clip;
}

.displayContainer {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    align-items: center;
    gap: 3em;
}

.displayItem {
    width: fit-content;
    height: fit-content;
    position: relative; 
    margin: 0 calc(1vw + 2em); 
}


//Container

//Content
.displayItemImageContainer {
    width: 50%;
    height: 40vh;
}

.displayItem img{
    width: auto;
    height: 100%;
}

.displayItem a {
    width: 100%;
    display: block; 
    color: #151531;
    text-decoration: none;
    text-align: center;
    font-family: Geometria;
    font-weight: 800;
    font-size: calc(0.5em + 0.5vw);
    margin-top: 1rem;
}

@media (max-width: 500px) {

    .projectsDisplayContainer {
        display: flex;
        align-items: center;
    }

    .displayItemImageContainer {
        width: 100%;
        height: fit-content;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .displayItem {
        width: 100%;
        margin: 0;
    }

    .projectsDisplayContainer {
        width: 80%;
        overflow: hidden;
    }

    .displayItem img{
        width: auto;
        height: 20vh;
    }

    .displayItemMobile {
        width: 10vh !important;
        margin: 0 auto;
    }
}