body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    background-color: #fafafa;
}

.flicking-viewport {
    height: 35vh;
}

::-webkit-scrollbar {
    width: 0px;
    background-color: transparent;
}

//Colors

$midnightBlue: #151531;
$purpleIndigo: #421A92;
$eletricPurple: #C051FF;
$cornFlowerBlue: #404BE3;
$dodgerBlue: #39BEFF;

//Colors end

//Size

$defaultScreenSize: 100vh;
$minSize: 730px;

//Size end

//Font

@font-face {
    font-family: Poppins;
    src: url(./Font/Poppins-Regular.ttf);
}

@font-face {
    font-family: Geometria;
    src: url(./Font/Geometria.woff);
}

@font-face {
    font-family: MontSerrat;
    src: url(./Font/Montserrat-Regular.ttf);
}

@font-face {
    font-family: MontSerratBold;
    src: url(./Font/Montserrat-Bold.ttf);
}

//Font end

//Media

@media (max-width: 499px) {

    ::-webkit-scrollbar {
        width: 0px;
        height: 0px;
    }
    
    ::-webkit-scrollbar-track {
        width: 0;
        height: 0;
    }
}