@import '../index.scss';

//Container 

.skillsContainer {
    width: 100%;
    height: min(48vw, $defaultScreenSize);
    min-height: $minSize;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #fafafa;
    position: relative;
    overflow-x: clip;
}

//Container end

//Content
.skillsContainer h1 {
    text-align: center;
    font-family: MontserratBold;
    color: $midnightBlue;
    font-size: calc(3vw + 1em);
}

.skillsList {
    width: 80%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.skill {
    display: flex;
    flex-direction: column;
    margin: 1em 2em;
}

.skill img {
    width: calc(10vw + 2em);
    transition: transform 500ms;
}

.skill img:hover {
    filter: drop-shadow(0 0 5px $eletricPurple);
    transform: scale(1.1);
    transition: transform 500ms;
}

.skill span {
    text-align: center;
    font-family: MontserratBold;
    color: $midnightBlue;
    font-size: calc(0.8vw + 0.5em);
}
