@import '../../../index.scss';

//Container

.desktopNavBarContainer{
    width: 80%;
    height: calc(6vw + 3em);
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    top: 1em;
    transition: 200ms;
}

//Container end

//Elements

.mainLogo {
    width: calc(4vw + 3em);
    animation: linear rotating 15s infinite;
}

.mainLogo:hover {
    width: calc(4vw + 3em);
    animation-play-state: paused;
}

.mainLogoContainer {
    width: fit-content;
    height: fit-content;
    position: relative;
}

.mainLogoContainer:after {
    width: calc(6vw + 3em);
    height: calc(3vw + 2em);
    display: block;
    content: 'Allan';
    position: absolute;
    right: calc(-10vw + -2.5em);
    top: 7%;
    color: transparent;
    font-family: Poppins;
    font-weight: 600;
    font-size: calc(2vw + 2em);
    opacity: 0;
    transition: opacity 500ms;
    background: linear-gradient(to right, #C051FF, #421A92);
    background-clip: text;
}

.mainLogoContainer:hover:after {
    opacity: 0.4;
    transition: opacity 5s;
}

.navBar {
    width: 35vw;
    min-width: fit-content;
    display: flex;
    justify-content: space-between;
}

.navOption {
    width: fit-content;
    position: relative;
    padding: 0 5px;
    color: #fff;
    font-size: calc(0.7vw + 0.6em);
    font-family: Poppins;
    font-weight: 600;
    user-select: none;
}

.navOption:hover {
    text-shadow: 0 0 5px #fff;
    cursor: pointer;
}

.navOption:after {
    content: '';
    width: 100%;
    height: 0px;
    position: absolute;
    bottom: -5px;
    left: 0;
    border-bottom: 2px solid $dodgerBlue;
    transform-origin: bottom right;
    transform: scaleX(0);
    transition: transform 400ms ease-out;
}

.navOption:hover:after {
    transform: scaleX(1);
    transform-origin: bottom left;
}

//Elements end

//Keyframe

@keyframes rotating {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(359deg);
    }
}


//Media 

@media (max-width: 699px) {

    //Container

    .desktopNavBarContainer{
        width: 90%;
        transition: 200ms;
        top: 3em;
    }  

    //Container end  
}

//Mobile 

@media (max-width: 499px) {

    //Container

    .desktopNavBarContainer{
        display: none;
    }  

    //Container end  
}