

//Container

.wavesContainer {
    width: 100%;
    height: 10vw;
    overflow: clip;
}

//Container end

.waves {
    width: 100%;
    height: 100%;
    overflow: clip;
    position: relative;
}

.purpleWaveContainer {
    width: auto;
    height: 100%;
    position: absolute;
    left: 0;
    bottom: -1vw;
    z-index: 999;
    display: flex;
}

.purpleWave {
    position: relative;
    z-index: 999;
    animation: linear toLeft 10s infinite;
}

.secondPurpleWave {
    margin-left: -5px;
}

.pinkWaveContainer {
    width: fit-content;
    height: 100%;
    position: absolute;
    right: 0;
    bottom: -1vw;
    display: flex;
}

.pinkWave {
    position: relative;
    z-index: 99;
    animation: linear toRight 15s infinite;
}

.secondPinkWave {
    margin-left: -3px;
}

//Keyframes

@keyframes toLeft {
    0% {
        transform: translateX(0);
    }

    100% {
        transform: translateX(-100%);
    }
}

@keyframes toRight {
    0% {
        transform: translateX(0);
    }

    100% {
        transform: translateX(100%);
    }
}

//mobile 

@media (max-width: 499px) {

    .secondPurpleWave {
        margin-left: -2px;
    }

    .secondPinkWave {
        margin-left: -2px;
    }

    .wavesContainer {
        height: 20vw;
    }
}