@import '../index.scss';

//Container 

.projectsContainer {
    width: 100%;
    height: min(48vw, $defaultScreenSize);
    min-height: $minSize;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fafafa;
    position: relative;
    overflow: clip;
}

//Container end

@media(max-width: 800px) {
    .projectsContainer {
        flex-direction: column;
        justify-content: space-evenly;
    }
}