@import '../index.scss';

//Container

.aboutContainer {
    width: 100%;
    height: max(48vw, $defaultScreenSize);
    min-height: 550px;
    margin-top: 9vw;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fafafa;
    position: relative;
}

//Container end

//Window adjust media

@media (max-width: 700px) {
    .aboutContainer {
        flex-direction: column;
        min-height: 800px;
    }
}