@import '../../../index.scss';


//Container
.aboutImageContainer {
    width: 35%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    transition: 250ms;
}

//Container end

//Element

.Image {
    width: 28vw;
    min-width: 12em;
    position: relative;
    margin-bottom:5em ;
    border-radius: 30px;
}

//Window adjust media

@media (max-width: 700px) {

    //Container 
    .aboutImageContainer {
        width: 80%;
        align-items: center;
    }

    //Content

    //Element

    .Image {
        min-width: 15em;
        z-index: 9;
    }
}