@import '../index.scss';

//Container

.introductionContainer {
    width: 100%;
    height: $defaultScreenSize;
    min-height: $minSize;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: $midnightBlue;
    position: relative;
    overflow-x: clip;
}

.contentContainer {
    width: 80%;
    height: 80%;
    display: flex;
    transition: 200ms;
}

//Container end

//Element

.wave {
    content: "";
    width: 100%;
    height: 10em;
    position: absolute;
    bottom: -10em;
    z-index: 9;
    background-image: url(../Images/Introduction/VisualElements/wave.svg);
    background-size: 100%;
    background-repeat: no-repeat;
    filter: drop-shadow(0 7px 3px #0000002c);
}


//Media

@media (max-width: 699px) {

    //Container

    .contentContainer {
        width: 90%;
        transition: 200ms;
    }

    //Container end  
}

//Media mobile

@media (max-width: 499px) {

    //Container

    .contentContainer {
       flex-direction: column;
    }

    //Container end  
}